/* Global box-sizing reset */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.heroSection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* padding: 20px; */
    margin-top: 20px;
    max-width: 1400px;
    width: 100%;
    margin: auto;
}

.topContainer {
    display: flex;
    flex-direction: row;
    gap: 20px;
    width: 100%;
    margin: auto;
}


/* Animation */


.textSection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #fff;
    border-radius: 22px;
    padding: 40px;
    background: #217AA3;
    gap: 100px;
    justify-content: space-between;

}

.textSection h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
    margin-top: 0px;
    width: 80%;
}

.textSection p {
    font-size: 1.2rem;
    width: 90%;
}

.contactButton {
    border-radius: 57.245px;
    background: #FFF;
    padding: 20px;
    font-size: 20px;
    width: 171.734px;
    height: 64.114px;
    border: #FFF;
    font-family: "GeneralSans M";
    color: #000;
}

.icons {
    display: inline-flex;
    align-items: center;
    gap: 28.622px;
}

.icons .icon {
    font-size: 2rem;
    margin-right: 10px;
}


.imageSection {
    border-radius: 22px;
    background-image: url('../../public/profile.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
}




/* downContainer */

.downContainer {
    display: flex;
    flex-direction: row;
    gap: 20px;
    width: 100%;
    margin: auto;
    margin-top: 20px;
}

.infoSection{
    background: #8DBD74;
    padding: 40px;
    color: #fff;
    border-radius: 22px;
    background-image: url('../../public/cloudimage.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "GeneralSans R";
    height: 150px;
    display: flex;
    align-items: center;

}

.infoSection p{
    width: 65%;
}

.infoSection a{
    color: white;
} 


.skillsSection {
    background: #217AA3;
    padding: 40px;
    color: #fff;
    border-radius: 20px;

    display: flex;
    align-items: center;
    gap: 40px;
    flex: 1 0 0;
    /* justify-content: space-between; */
    
}


.skillsSection div{
    display: flex;
    align-items: center;
    gap: 10px;
    flex: 1 0 0;
    justify-content: space-between;
}
.skillsSection img {
    font-size: 2rem;
    margin-right: 10px;
    
} 





/* Responsive Styling */
@media (min-width: 992px) {
    .textSection {
        max-width: 700px;
        /* padding-right: 100px; */
    }

    .infoSection{
        max-width: 700px;
    }
}

@media (min-width: 768px) {
    .textSection {
        min-width: 500px;
    }

    .infoSection{
        min-width: 500px;
    }
}




@media (max-width: 768px) {
    .heroSection{
        max-width: 100%;
        overflow: hidden;
        padding: 20px;
        /* width: 90vw; */
    }

    .topContainer {
        flex-direction: column;
        max-width: 100%;
    }

    .textSection {
        order: 2;
    }

    .imageSection {
        height: 40vh;
        order: 1;
    }

    /* Text Content with Buutton */
    .textSection{
        gap: 20px;
    }

    .textSection h1 {
        font-size: 1.8rem;
        margin-bottom: 10px;
        margin-top: 0px;
        width: 100%;
        line-height: 1;
        letter-spacing: -1px;
    }

    .textSection p {
        font-size: 1rem;
        width: 100%;
    }

    .contactButton {
        border-radius: 50px;
        padding: 0px;
        font-size: 18px;
        width: 100%;
        height: 50px;
        
    }

    .icons {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
    }

    .icons .icon {
        font-size: 2rem;
        margin-right: 10px;
    }

    /* downContainer */
    .downContainer{
        flex-direction: column;
    }

    .infoSection{
        /* font-size: 1em; */
    }

    .infoSection p {
        width: 100%;
        font-size: 0.8em;
    }

    .infoSection a {
        color: white;
    }

    .skillsSection {
        gap: 5px;
        flex-direction: column;

    }

    .skillsSection img {
        font-size: 2rem;
        margin-right: 10px;
        /* width: 20%; */

    }
}

@media (max-width: 480px) {

    .topContainer{
        max-width: 100%;
    }

}