/* Global box-sizing reset */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.caseStudySection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px;
    margin-top: 20px;
    max-width: 1400px;
    width: 100%;
    margin: auto;
    margin-top: 50px;
    background: #EEF5FF;
    border-radius: 22px;
}

.coverImage {
    border-radius: 22px;
    background-image: url('../../public/clipperlinkcover.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 390px;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 40px;
    flex-direction: column;
}


.textContainer {
    text-align: right;
}

.heading h1 {
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: "GeneralSans B";
    font-size: 40px;
    margin: 0;
    line-height: 1.2;
    color: #fff;
}

.heading p {
    font-family: "GeneralSans R";
    font-size: 16px;
    margin-top: 10px;
    color: #fff;
    text-align: right;
}


/* Grid Content */


.Grid {
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 100%;
    margin: auto;
    margin-top: 30px;


}

.gridContainer {
    display: flex;
    flex-direction: row;
    gap: 30px;
}

.firstGrid {
    display: flex;
    /* width: 655px; */
    padding: 50px 40px;

    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    border-radius: 22px;
    background: #fff;
}






/* Text in Grid */
.firstGrid h1 {
    color: #006BF9;
    font-family: "GeneralSans B";
    font-size: 30px;
        margin-top: 0px;
            margin-bottom: 0px;
}

.firstGrid p {
    color: #585858;
    font-family: "GeneralSans R";
    font-size: 25px;
    line-height: normal;
        margin-top: 0px;
            margin-bottom: 0px;
}


.btn {
    display: flex;
    width: 256px;
    height: 64px;
    padding: 11.449px;
    justify-content: center;
    align-items: center;
    gap: 11.449px;
    border-radius: 50px;
    color: rgb(0, 0, 0);

    background: #006BF9;

    color: #fff;

    font-feature-settings: 'liga' off, 'clig' off;
    font-family: "GeneralSans M";
    font-size: 22.898px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -1.145px;
    border: #006BF9;
    cursor: pointer;
}

.btnContainer {

    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    margin-top: 80px;
}








/* Responsive Styling */
@media (min-width: 992px) {
    .caseStudySection {
        /* max-width: 700px; */
        /* padding-right: 100px; */
        /* padding: 30px; */
    }

    .Grid {
        /* max-width: 700px; */
    }
}



@media (max-width: 768px) {
    .caseStudySection {
        padding: 10px;
    }

    .coverImage {
        border-radius: 22px;
        background-image: url('../../public/mobileClipperlinkcover.png');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 390px;
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        padding: 0px;
        flex-direction: column;
        /* border-radius: 22px; */
    }

    .heading {
        background-color: #B4C6EC;
        border-radius: 0px 0px 22px 22px;
        width: 100%;
        padding: 20px;
        padding-left: 30px;

    }

    .heading h1 {
        font-size: 30px;
        color: #000;
    }

    .heading p {
        margin-top: 0px;
        text-align: left;
        color: #000;
    }

    .gridContainer {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    .firstGrid {
        padding: 30px 20px;
    }

    .firstGrid h1 {
        font-family: "GeneralSans B";
        font-size: 23px;
    }

    .firstGrid p {
        font-family: "GeneralSans R";
        font-size: 18px;
        line-height: normal;
        margin-top: -20px;
    }

    .btn {
        display: flex;
        width: 170px;
        height: 64px;
        padding: 11.449px;
        font-family: "GeneralSans M";
        font-size: 18px;
    }

    .btnContainer {
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        margin-top: 20px;
    }

    .gridContainer {
        padding: 0px;
    }

    .firstGrid {
        margin-top: -10px;
    }
}

@media (max-width: 480px) {

    .caseStudySection {
        max-width: 100%;
    }

}