/* Global box-sizing reset */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.designWorksSection{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px;
    margin-top: 20px;
    max-width: 1400px;
    width: 100%;
    margin: auto;
    margin-top: 50px;
    /* background: #EFEFEF; */
    border-radius: 22px;

}

.galleryContainer{
    display: flex;
}


.designWorksSection h1 {
    font-family: "GeneralSans B";
    font-size: 40px;
    margin-bottom: 30px;
    margin-top: 50px;
}

/* Grid Content */


.Grid {
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 100%;
    margin: auto;
    margin-top: 30px;
}

.gridContainer {
    display: flex;
    flex-direction: row;
    gap: 30px;
}

.firstGrid {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    border-radius: 22px;
    background: #FBFCFD;
}

.firstGrid img{
    width: 100%;
    border-radius: 20px;
}













/* Responsive Styling */
@media (min-width: 992px) {
    .caseStudySection {
        /* max-width: 700px; */
        /* padding-right: 100px; */
        /* padding: 30px; */
    }

    .Grid {
        /* max-width: 700px; */
    }
}



@media (max-width: 768px) {


    .gridContainer {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    .firstGrid {
        padding: 30px 20px;
    }


    .gridContainer {
        padding: 0px;
    }

    .firstGrid {
        margin-top: -10px;
    }
}

@media (max-width: 480px) {

    .caseStudySection {
        max-width: 100%;
    }

}