/* Global box-sizing reset */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.caseStudySection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px;
    margin-top: 20px;
    max-width: 1400px;
    width: 100%;
    margin: auto;
    margin-top: 50px;
    background: #11100F;
    border-radius: 22px;
}

.coverImage {
    border-radius: 22px;
    background-image: url('../../public/mgcover.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 390px;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 40px;
    flex-direction: column;
}


.textContainer {
    text-align: right;
}

.heading h1 {
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: "GeneralSans B";
    font-size: 40px;
    margin: 0;
    line-height: 1.2;
    color: #fff;
}

.heading p {
    font-family: "GeneralSans R";
    font-size: 16px;
    margin-top: 10px;
    color: #fff;
    text-align: right;
}


/* Grid Content */


.Grid {
    display: flex;
    flex-direction: row;
    gap: 30px;
    width: 100%;
    margin: auto;
    margin-top: 30px;


}

.gridContainer {
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 50%;
}

.firstGrid {
    display: flex;
    /* width: 655px; */
    padding: 50px 40px;

    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    border-radius: 22px;
    background: #242424;
}

.textSection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #fff;
    border-radius: 22px;
    padding: 40px;
    background: linear-gradient(222deg, #1681AB 19.65%, #2E709A 81.59%), #7E9AFE;
    gap: 100px;
    justify-content: space-between;

}

.textSection h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
    margin-top: 0px;
    width: 80%;
}

.textSection p {
    font-size: 1.2rem;
    width: 90%;
}

.contactButton {
    border-radius: 57.245px;
    background: #FFF;
    padding: 20px;
    font-size: 20px;
    width: 171.734px;
    height: 64.114px;
    border: #FFF;
    font-family: "GeneralSans M";
    color: #000;
}

.icons {
    display: inline-flex;
    align-items: center;
    gap: 28.622px;
}

.icons .icon {
    font-size: 2rem;
    margin-right: 10px;
}



/* Text in Grid */
.firstGrid h1 {
    color: #F2620F;
    font-family: "GeneralSans B";
    font-size: 30px;
        margin-top: 0px;
            margin-bottom: 0px;
}

.firstGrid p {
    color: #E8E8E8;
    font-family: "GeneralSans R";
    font-size: 25px;
    line-height: normal;
        margin-top: 0px;
            margin-bottom: 0px;
}


.btn {
    display: flex;
    width: 256px;
    height: 64px;
    padding: 11.449px;
    justify-content: center;
    align-items: center;
    gap: 11.449px;
    border-radius: 50px;
    color: rgb(255, 255, 255);

    background: #F2620F;

    font-feature-settings: 'liga' off, 'clig' off;
    font-family: "GeneralSans M";
    font-size: 22.898px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -1.145px;
    border: #F2620F;
    cursor: pointer;
}

.btnContainer {

    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    margin-top: 80px;
}








/* Responsive Styling */
@media (min-width: 992px) {
    .caseStudySection {
        /* max-width: 700px; */
        /* padding-right: 100px; */
        /* padding: 30px; */
    }

    .Grid {
        /* max-width: 700px; */
    }
}



@media (max-width: 768px) {
    .Grid{
        flex-direction: column;
    }
    .gridContainer{
        width: 100%;
    }
    .caseStudySection {
        padding: 10px;

    }

    .coverImage {
        border-radius: 22px;
        background-image: url('../../public/mobileMgcover.png');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 390px;
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        padding: 0px;
        flex-direction: column;
        /* border-radius: 22px; */
    }

    .heading {
        background-color: #242424;
        border-radius: 0px 0px 22px 22px;
        width: 100%;
        padding: 20px;
        padding-left: 30px;

    }

    .heading h1 {
        color: #E8E8E8;
        font-size: 30px;
    }

    .heading p {
        color: #E8E8E8;
        margin-top: 0px;
        text-align: left;
    }

    .gridContainer {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    .firstGrid {
        padding: 30px 20px;
    }

    .firstGrid h1 {
        font-family: "GeneralSans B";
        font-size: 23px;
    }

    .firstGrid p {
        font-family: "GeneralSans R";
        font-size: 18px;
        line-height: normal;
        margin-top: -20px;
    }

    .btn {
        display: flex;
        width: 170px;
        height: 64px;
        padding: 11.449px;
        font-family: "GeneralSans M";
        font-size: 18px;
    }

    .btnContainer {
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        margin-top: 20px;
    }

    .gridContainer {
        padding: 0px;
    }

    .firstGrid {
        margin-top: -10px;
    }
}

@media (max-width: 480px) {

    .caseStudySection {
        max-width: 100%;
    }

}