/* Global box-sizing reset */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.navbar {
    position: sticky;
    top: 0;
    background-color: #fff;
    justify-content: center;
    z-index: 1000;
    width: 100%;
    margin: 0 auto;
    padding: 20px;
    overflow: hidden;
}

.navList {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.navItem {
    flex-grow: 1;
    text-align: center;
    padding: 23px 0px;
    border-left: 1px solid rgba(255, 255, 255, 0.50);
    cursor: pointer;
}

.navItem a {
    text-decoration: none;
    color: #0A0B29;
}

.navItemLink.active {
    background-color: var(--primary-color);
    color: white;
}

.navItemSelected {
    background-color: var(--primary-color);
}

/* Burger Menu */
.burger {
    display: none;
    flex-direction: column;
    justify-content: space-around;
    width: 30px;
    height: 30px;
    cursor: pointer;
    z-index: 1100;
    position: relative;
}

.burgerLine {
    width: 100%;
    height: 4px;
    background-color: #0A0B29;
    transition: all 0.3s ease;
}

.burgerLineOpen1 {
    transform: rotate(45deg);
    position: absolute;
    top: 12px;
}

.burgerLineOpen2 {
    opacity: 0;
    /* Hide the middle line */
}

.burgerLineOpen3 {
    transform: rotate(-45deg);
    position: absolute;
    top: 12px;
}

/* Mobile Navigation */
@media (max-width: 768px) {
    .navbar{
        max-width: 100vw;
    }
    .navList {
        display: none;
        flex-direction: column;
        position: absolute;
        top: 60px;
        /* Adjust this to be below the navbar */
        left: 0;
        width: 100%;
        background-color: #fff;
        z-index: 1000;
    }

    .navListOpen {
        display: flex;
    }

    .navItem {
        border-left: none;
        padding: 15px;
    }

    .burger {
        display: flex;
        width: 30px;
        height: 30px;
        /* padding: 20px; */
    }
}

/* Further small adjustments for mobile */
@media (max-width: 480px) {
    .navItem a {
        font-size: 15px;
    }
}